.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.logo {
  width: 120px;
  height: 31px;
  background: #333;
  border-radius: 6px;
  margin: 16px 28px 16px 0;
  float: left;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.table {
  border: 1px solid #e9e9e9;
  border-collapse: collapse;
  width: 100%;
}

.table th {
  background: #f7f7f7;
}

.table td, .table th {
  border: 1px solid #e9e9e9;
  padding: 8px 16px;
  text-align: left;
}

.table td:first-child {
  background: #fcfcfc;
  font-weight: 500;
  width: 20%;
  font-family: Lucida Console, Consolas, Menlo, Courier, monospace;
}

/* 表格数据异常时的样式 */
.table-row-warnning {
  background-color: #fff6e6;
}

.table-row-disabled {
  background-color: #f3f3f3;
}

/*
  定义了一系列控制间距的样式，全部以 em 为单位，即根据 font-size 设置
  em与px换算公式为: px / font-size = em
  以 font-size = 12px 为例
  small  : 4px  / 12px = 0.3333em;
  middle : 8px  / 12px = 0.6666em;
  large  : 16px / 12px = 1.3333em;
  huge   : 32px / 12px = 2.6666em;
*/
/* 元素并列时与左侧的间距 */
.margin-left-small {
  margin-left: 0.3333em;
}

.margin-left-middle {
  margin-left: 0.6666em;
}

.margin-left-large {
  margin-left: 1.3333em;
}

.margin-left-huge {
  margin-left: 2.6666em;
}

/* 元素并列时与右侧的间距 */
.margin-right-small {
  margin-right: 0.3333em;
}

.margin-right-middle {
  margin-right: 0.6666em;
}

.margin-right-large {
  margin-right: 1.3333em;
}

.margin-right-huge {
  margin-right: 2.6666em;
}

/* 内边距设置 */
.padding-around-small {
  padding: 0.3333em;
}

.padding-around-middle {
  padding: 0.6666em;
}

.padding-around-large {
  padding: 1.3333em;
}

.padding-around-huge {
  padding: 2.6666em;
}

/* 外边距设置 */
.margin-around-small {
  margin: 0.3333em;
}

.margin-around-middle {
  margin: 0.6666em;
}

.margin-around-large {
  margin: 1.3333em;
}

.margin-around-huge {
  margin: 2.6666em;
}

.margin-top-bottom-small {
  margin-top: 0.3333em;
  margin-bottom: 0.3333em;
}

.margin-top-bottom-middle {
  margin-top: 0.6666em;
  margin-bottom: 0.6666em;
}

.margin-top-bottom-large {
  margin-top: 1.3333em;
  margin-bottom: 1.3333em;
}

.margin-top-bottom-huge {
  margin-top: 2.6666em;
  margin-bottom: 2.6666em;
}

.margin-top-small {
  margin-top: 0.3333em;
}

.margin-top-middle {
  margin-top: 0.6666em;
}

.margin-top-large {
  margin-top: 1.3333em;
}

.margin-top-huge {
  margin-top: 2.6666em;
}

.margin-bottom-small {
  margin-bottom: 0.3333em;
}

.margin-bottom-middle {
  margin-bottom: 0.6666em;
}

.margin-bottom-large {
  margin-bottom: 1.3333em;
}

.margin-bottom-huge {
  margin-bottom: 2.6666em;
}

/* 选择框 */
/* 只容纳一个字，如: 日、时 */
.select-width-mini {
  width: 4em;
}

/* 正常的宽度 */
.select-width-default {
  width: 16.6666em;
}

.display-flex {
  display: 'flex' !important;
}

/* 使用flex布局时默认左对齐，使单独一个元素右对齐，需要在父元素上使用 display: flex */
.justify-self-right {
  margin-left: auto;
}

/* 水平尾部对齐 */
.flex-justify-content-end {
  display: 'flex';
  -ms-flex-pack: 'flex-end';
      justify-content: 'flex-end';
}

/* 水平两端对齐 */
.flex-justify-content-space-between {
  display: 'flex';
  -ms-flex-pack: 'space-between';
      justify-content: 'space-between';
}

/* 每个项目两侧间隔相等 */
.flex-justify-content-space-around {
  display: 'flex';
  -ms-flex-pack: 'space-around';
      justify-content: 'space-around';
}

/* 垂直居中 */
.flex-align-items-center {
  display: 'flex';
  -ms-flex-align: 'center';
      align-items: 'center';
}

.font-size-small {
  font-size: 0.3333em;
}

.font-size-middle {
  font-size: 0.6666em;
}

.font-size-large {
  font-size: 1.3333em;
}

.font-size-huge {
  font-size: 2.6666em;
}

.font-color-red {
  color: #f04134;
}

.font-color-yellow {
  color: #ffbf00;
}

.border-color-red, .border-color-red:hover {
  border-color: #f04134;
}

.border-color-yellow {
  border-color: #ffbf00;
}

/* 光标形状恢复默认 */
.cursor-default {
  cursor: default;
}

/* 光标变成手形状 */
.cursor-pointer {
  cursor: pointer;
}

.width-max {
  width: 100%;
}

.background-color-white {
  background-color: '#fff';
}

/* 解决小型表格的表头 padding 生成的左右露出背景色的问题 */
.ant-table-small .ant-table-header>table, .ant-table-small .ant-table-body>table {
  background-color: #fff;
}

/* 灰色字体，通常用于描述文字 */
.description-text {
  color: #999;
}


